<template>
  <section 
    id="site-footer"
    ref="sectionRef"
    :class="{ animate: isVisible }"
  >
    <!-- QR Code -->
    <img
      class="qr-code"
      :src="pictQRcode"
      alt="QR code"
      @click="handleQRClick"
    />

    <!-- Footer List -->
    <ul class="footer-list">
      <li :style="{ '--i': 1 }" :class="{ animate: isVisible }">
        <a
          class="footer-link"
          href="https://blog.sunnytseng.com/about.html"
          target="_blank"
        >
          Contact Me
        </a>
      </li>
      <li :style="{ '--i': 2 }" :class="{ animate: isVisible }">
        &copy; 2024 Sunny Tseng. All Rights Reserved.
      </li>
    </ul>
  </section>
</template>

<script>
import { ref, onMounted, onUnmounted } from "vue";
import pictQRcode from "@/assets/pictQRcode.jpg";

export default {
  name: "HomeFooterSection",
  setup() {
    const sectionRef = ref(null);
    const isVisible = ref(false);

    const handleQRClick = () => {
      window.open("https://sunnytseng.com", "_blank");
    };

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            isVisible.value = true;
          } else {
            isVisible.value = false;
          }
        });
      },
      { threshold: 0 }
    );

    onMounted(() => {
      if (sectionRef.value) observer.observe(sectionRef.value);
    });

    onUnmounted(() => {
      if (sectionRef.value) observer.unobserve(sectionRef.value);
    });

    return {
      sectionRef,
      isVisible,
      pictQRcode,
      handleQRClick,
    };
  },
};
</script>

<style scoped>
/********************
 * 動畫效果
 ********************/
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes qrCodeFloat {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/********************
 * 整體樣式
 ********************/
#site-footer {
  background-color: #171921;
  height: 500px;
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px;
  color: white;
  border-radius: 40px;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

#site-footer.animate {
  opacity: 1;
  transform: translateY(0);
}

/********************
 * QR Code 樣式
 ********************/
.qr-code {
  border-radius: 10px;
  height: 70px;
  margin-bottom: 30px;
  animation: qrCodeFloat 2s ease-in-out infinite;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.qr-code:hover {
  transform: scale(1.2);
  box-shadow: 0 0 15px 5px rgba(240, 165, 0, 0.7);
}

/********************
 * Footer List 樣式
 ********************/
.footer-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-list li {
  color: white;
  font-size: 18px;
  margin-bottom: 10px;
  line-height: 1.5;
  opacity: 0;
  transform: translateY(20px);
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
}

.footer-list li.animate {
  opacity: 1;
  transform: translateY(0);
  animation: fadeIn 1s ease-in-out forwards;
  animation-delay: calc(var(--i) * 0.2s);
}

.footer-list li:hover {
  color: gray;
}

/********************
 * Footer Link 樣式
 ********************/
.footer-link {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease, box-shadow 0.3s ease;
}

.footer-link:hover {
  text-decoration: underline;
  color: gray;
  box-shadow: 0 0 100px 5px rgba(240, 165, 0, 0.7);
}

/********************
 * 響應式設計
 ********************/
@media (max-width: 768px) {
  #site-footer {
    padding: 20px;
    height: auto;
  }

  .qr-code {
    margin-bottom: 20px;
  }

  .footer-list li {
    font-size: 16px;
  }
}
</style>

