<template>
  <div class="sidebar" v-show="!isMobile">
    <ul>
      <li><a href="#main" @click="scrollToSection('main')">Top</a></li>
      <li><a href="#background" @click="scrollToSection('background')">程式背景</a></li>
      <li><a href="#portfolio" @click="scrollToSection('portfolio')">作品集</a></li>
      <li><a href="#technology" @click="scrollToSection('technology-connection')">技術連結</a></li>
      <li><a href="#qa" @click="scrollToSection('qa-section')">問與答</a></li>
      <li><a href="#footer" @click="scrollToSection('side-footer')"></a></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'AppSidebar',
  data() {
    return {
      isMobile: false
    };
  },
  mounted() {
    this.checkScreenWidth();
    window.addEventListener("resize", this.checkScreenWidth);
  },
  beforeUnmount() { // 使用 Vue 3 的新生命週期鉤子
    window.removeEventListener("resize", this.checkScreenWidth);
  },
  methods: {
    scrollToSection(sectionId) {
      const element = document.getElementById(sectionId);
      if (element) {
        window.scrollTo({
          top: element.offsetTop,
          behavior: 'smooth'
        });
      }
    },
    checkScreenWidth() {
      this.isMobile = window.innerWidth <= 768;
    }
  }
};
</script>

<style scoped>
.sidebar {
  position: fixed;
  top: 20%;
  left: 20px;
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  margin: 10px 0;
}

.sidebar a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.sidebar a:hover {
  color: #007bff;
